.cart-summary{
    padding-inline: $spacer-2;
    border-top: 1px solid $primary;
    h2{
        margin-bottom: $spacer-2;
    }
}
.cart-product{
    padding: $spacer-2;
    border: 1px solid $accent;
    h3{
        font-size: $fs-150;
    }
    .pricing-card-price{
        font-size: $fs-125;
    }
}
@media(min-width: $md){
    .cart-summary{
        border-left: 1px solid $primary;
        border-top: none;
    }
}

//confirmation page
.cart-confirmation{
    border: 1px solid $primary;
    border-radius: $border-radius;
}
//cart icon in menu bar
.nav-cart .feather{
    font-size: 1.375rem;
}
//place order page, shows a confirmation of whats been ordered and an order ref number
.place-order .std-card{
    align-items: flex-start;
}
.nav-menu .nav-cart{
    position: relative;
   display: block;
    margin-inline: auto;
    width: fit-content;
}
.cart-item-count{
    $fs: 8px;
    $padding: $spacer-2;
    font-size: $fs;
    display: grid;
    place-items: center;
    color: $primary-950;
    background-color: $primary;
    border-radius: 100%;
    font-weight: 700;
    width: calc($fs * 2);
    height: calc($fs * 2);
position: absolute;
right: 0;
top: 5px;

}
